import React from 'react';

function Title({ valor }) {
  const titleStyle = {
    fontSize: '24px', // Tamao de fuente grande
    fontWeight: 'lighter', // Fuente ms fina
    textAlign: 'center', // Centra el texto horizontalmente
    marginTop: '10px', // Espaciado superior
  };

  return (
    <h1 style={titleStyle}> 
      Objeto del Contrato Relacionado Con : <strong>{valor} </strong>
    </h1>
  );
}

export default Title;
