import React, { useState, useEffect } from "react";
import Graph from "react-graph-vis";
import { Button } from '@mui/base/Button';

function Bapp() {
  return <Button>Click Me</Button>;
}

export function Grafo({ apiData }) {
  const [selectedNodes, setSelectedNodes] = useState([]);
  const [selectedNodesInfo, setSelectedNodesInfo] = useState([]);
  const [isPhysicsEnabled, setIsPhysicsEnabled] = useState(false);
  const [graphData, setGraphData] = useState({ nodes: [], edges: [] });

  const uniqueNodes = {};

  const options = {
    layout: {},
    physics: {
      enabled: isPhysicsEnabled,
      forceAtlas2Based: {
        springLength: 100,
      },
      stabilization: {
        enabled: true,
        iterations: 1,
      },
      minVelocity: 0.75,
      solver: "forceAtlas2Based",
    },
    edges: {
      color: {
        inherit: true,
      },
      selfReferenceSize: null,
      selfReference: {
        angle: 0.7853981633974483,
      },
    },
    height: "600px",
  };

  const events = {
    select: function (event) {
      const { nodes } = event;
      setSelectedNodes(nodes);

      const selectedNodesData = [];
      nodes.forEach((node) => {
        const matchingNode = apiData.find(
          (item) => item.ruc_proveedor === node || item.ruc_ep === node
        );
        if (matchingNode) {
          selectedNodesData.push(matchingNode);
        }
      });

      setSelectedNodesInfo(selectedNodesData);
    },
  };

  const togglePhysics = () => {
    setIsPhysicsEnabled(!isPhysicsEnabled);
  };

  useEffect(() => {
    if (apiData) {
      const updatedData = { nodes: [], edges: [] };

      apiData.forEach((item) => {
        if (!uniqueNodes[item.ruc_proveedor]) {
          uniqueNodes[item.ruc_proveedor] = true;
          const newNodeProveedor = {
            id: item.ruc_proveedor,
            color: "rgb(255, 0, 0)",
          };
          updatedData.nodes.push(newNodeProveedor);
        }

        if (!uniqueNodes[item.ruc_ep]) {
          uniqueNodes[item.ruc_ep] = true;
          const newNodeEp = {
            id: item.ruc_ep,
          };
          updatedData.nodes.push(newNodeEp);
        }

        const newEdge = {
          from: item.ruc_proveedor,
          to: item.ruc_ep,
        };
        updatedData.edges.push(newEdge);
      });

      setGraphData(updatedData);
    }
  }, [apiData]);

  return (
    <div>
<div style={{ textAlign: 'center', margin: '20px 0' }}>
  <button
    onClick={togglePhysics}
    style={{
      fontSize: '14px', // Tamao de fuente ms pequeo
      padding: '8px 16px', // Relleno reducido
      backgroundColor: '#007BFF',
      color: 'white',
      border: 'none',
      borderRadius: '5px',
      cursor: 'pointer',
    }}
  >
    {isPhysicsEnabled ? "Estabilizar Grafo" : "Estructurar Grfico"}
  </button>
</div>


      <Graph
        graph={graphData}
        options={options}
        events={events}
        getNetwork={(network) => {}}
      />
      

{/*       
      <div>
        <h2>Informacin de Nodos Seleccionados</h2>
        <ul>
          {selectedNodesInfo.map((node) => (
            <li key={node.ruc_proveedor}>
              <strong>ID del Proveedor:</strong> {node.ruc_proveedor}
            </li>
          ))}
        </ul>
      </div> */}


    </div>
  );
}
