import React from 'react';

function Cabecera() {
  const cabeceraStyle = {
    fontSize: '36px', // Tamao de fuente grande
    fontWeight: 'lighter', // Fuente ms fina
    textAlign: 'center', // Centra el texto horizontalmente
    marginBottom: '20px', // Espaciado inferior
  };

  return (
    <h1 style={cabeceraStyle}>
      Conecta Compras Pblicas
    </h1>
  );
}

export default Cabecera;
