import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';

import { Grafo } from './vis'

import InputForm from './InputForm';
import SelectedNodesInfo from './SelectedNodesInfo';
import Title from './Title'; // Importa el componente Title
import Cabecera from './nameWeb'; // Importa el componente Title

function App() {
  const [apiData, setApiData] = useState(null);
  const [selectedNodesInfo, setSelectedNodesInfo] = useState([]);
  const [searchValue, setSearchValue] = useState('Carro'); // Estado para almacenar el valor de bsqueda

  const fetchData = (valor) => {
    // Realiza la solicitud a la API con el valor ingresado
    fetch(`http://127.0.0.1:8000/?valor=${valor}`)
      .then((response) => response.json())
      .then((data) => {
        setApiData(data);
      })
      .catch((error) => {
        console.error('Error al obtener los datos de la API:', error);
      });
  };

  useEffect(() => {
    // Realiza la solicitud inicial a la API cuando el componente se monta
    fetchData(searchValue);
  }, [searchValue]); // Ejecuta fetchData cuando searchValue cambia

  return (
    <div>
     
      {/* Renderiza el componente Title con el valor de bsqueda */}
      <Title valor={searchValue} />

      {/* Renderiza el componente Grafo con los datos de la API */}
      <Grafo apiData={apiData} className="shadowed-graph" />

            {/* Renderiza el componente InputForm */}
        <InputForm onInputChange={setSearchValue} />


      {/* Renderiza el componente SelectedNodesInfo con la informacin de los nodos seleccionados */}
      {/* <SelectedNodesInfo selectedNodesInfo={selectedNodesInfo} /> */}
      
    </div>
  );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <>
    {/* Renderiza el componente App */}

    <Cabecera/>
    <App />
    
    
  </>
);

reportWebVitals();
